.preloader {
  width: 100%;
  height: 100%;
  justify-content: center;
  align-items: center;
  transition: all .5s;
  display: flex;
  position: fixed;
  top: 0;
}

.loader {
  height: 20px;
  width: 140px;
  background-image: linear-gradient(#fff 20px, rgba(0, 0, 0, 0) 0), linear-gradient(#fff 20px, rgba(0, 0, 0, 0) 0), linear-gradient(#fff 20px, rgba(0, 0, 0, 0) 0), linear-gradient(#fff 20px, rgba(0, 0, 0, 0) 0);
  background-position: 0 0, 40px 0, 80px 0, 120px 0;
  background-repeat: no-repeat;
  background-size: 20px;
  animation: pgfill 1s linear infinite;
  display: block;
  position: relative;
}

@keyframes pgfill {
  0% {
